import "../style/style.css";
import Card from "./Card";
import { useRef } from "react";
// import { Alert } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import { useState } from "react";

const Sector = () => {
  const [realRS, setRealRS] = useState(0);
  const [artsRS, setartsRS] = useState(0);
  const [equtiyRS, setequtiyRS] = useState(0);
  const [datRS, setdatRS] = useState(0);
  const [naRS, setnaRS] = useState(0);

  const realEstatevalue = useRef();
  const userAmount = useRef();

  const artsvalue = useRef();
  const equityvalue = useRef();
  const datvalue = useRef();
  const commodity = useRef();

  const checkthefield = () => {
    let entereduseramount = parseInt(userAmount.current.value);
    let enteredrealEstate = parseInt(realEstatevalue.current.value);
    let enteredarts = parseInt(artsvalue.current.value);
    let enteredequity = parseInt(equityvalue.current.value);
    let entereddat = parseInt(datvalue.current.value);
    let enteredcommodity = parseInt(commodity.current.value);
    let sum = 0;

    if (enteredrealEstate.length == 0) {
      enteredrealEstate = 0;
    }
    if (enteredarts.length == 0) {
      enteredarts = 0;
    }
    if (enteredequity.length == 0) {
      enteredequity = 0;
    }
    if (entereddat.length == 0) {
      entereddat = 0;
    }
    if (enteredcommodity.length == 0) {
      enteredcommodity = 0;
    }
    sum =
      +enteredrealEstate +
      +enteredarts +
      +enteredequity +
      +entereddat +
      +enteredcommodity;
    if (sum > 100) {
      alert("more then 100");
      commodity.current.value = 0;
    }
    console.log(sum);

    console.log((enteredrealEstate / 100) * entereduseramount);

    setRealRS((enteredrealEstate / 100) * entereduseramount);

    setartsRS((enteredarts / 100) * entereduseramount);
    setequtiyRS((enteredequity / 100) * entereduseramount);
    setdatRS((entereddat / 100) * entereduseramount);
    setnaRS((enteredcommodity / 100) * entereduseramount);
  };

  const autofillincommodity = () => {
    //let enteredcommodity = parseInt(commodity.current.value);
    // if (commodity.current.value.length == 0) {
    //   commodity.current.value = 0;
    // }
    let entereduseramount = parseInt(userAmount.current.value);
    let enteredrealEstate = parseInt(realEstatevalue.current.value);
    let enteredarts = parseInt(artsvalue.current.value);
    let enteredequity = parseInt(equityvalue.current.value);
    let entereddat = parseInt(datvalue.current.value);
    let sum = 0;

    if (enteredrealEstate.length == 0) {
      enteredrealEstate = 0;
    }
    if (enteredarts.length == 0) {
      enteredarts = 0;
    }
    if (enteredequity.length == 0) {
      enteredequity = 0;
    }
    if (entereddat.length == 0) {
      entereddat = 0;
    }

    sum = +enteredrealEstate + +enteredarts + +enteredequity + +entereddat;
    // commodity.current.value = 100 - sum;
    let commodity = 100 - sum;
    if (parseInt(sum) + parseInt(commodity) == 100 && commodity >= 0) {
      console.log("sum" + (parseInt(sum) + parseInt(commodity)));

      document.getElementById("commodityfield").value = parseInt(100 - sum);
      console.log((enteredrealEstate / 100) * entereduseramount);

      setRealRS((enteredrealEstate / 100) * entereduseramount);

      setartsRS((enteredarts / 100) * entereduseramount);
      setequtiyRS((enteredequity / 100) * entereduseramount);
      setdatRS((entereddat / 100) * entereduseramount);
      setnaRS(((100 - sum) / 100) * entereduseramount);
    } else {
      alert("more then 100");
      document.getElementById("commodityfield").value = 0;
      setRealRS(0);

      setartsRS(0);
      setequtiyRS(0);
      setdatRS(0);
      setnaRS(0);
    }
  };

  return (
    <>
      <div className="rapinCont">
        <label className="head" style={{ fontSize: "1.5rem" }}>
          Name
        </label>

        <input type="text" className="head" />

        <label className="head" style={{ fontSize: "1.5rem" }}>
          Amount
        </label>
        <input type="number" className="head" ref={userAmount} />
      </div>
      <br />
      <Card>
        <table className="maintable">
          <tr>
            <td>
              <NavLink to="/realestate">RealEstate</NavLink>
            </td>
            <td>
              <NavLink to="/arts">Arts</NavLink>
            </td>
            <td>
              <NavLink to="/equity">Equity</NavLink>
            </td>
            <td>
              <NavLink to="/Debt">Debt</NavLink>
            </td>
            <td>
              <NavLink to="/commodity">Commodity</NavLink>
            </td>
          </tr>
          <tr>
            <td>
              <input
                type="number"
                ref={realEstatevalue}
                //onChange={autofillincommodity}
                required
              />
            </td>
            <td>
              <input
                type="number"
                ref={artsvalue}
                // onChange={autofillincommodity}
                required
              />
            </td>
            <td>
              <input
                type="number"
                ref={equityvalue}
                // onChange={autofillincommodity}
                required
              />
            </td>
            <td>
              <input
                type="number"
                ref={datvalue}
                onChange={autofillincommodity}
                onBlur={autofillincommodity}
                required
              />
            </td>
            <td>
              <input
                id="commodityfield"
                type="number"
                ref={commodity}
                // onChange={checkthefield}
                required
              />
            </td>
          </tr>
          <tr>
            <td style={{ fontSize: "2rem", fontWeight: "bold" }}>
              {parseInt(realRS).toFixed(1)}
            </td>

            <td style={{ fontSize: "2rem", fontWeight: "bold" }}>
              {parseInt(artsRS).toFixed(1)}
            </td>
            <td style={{ fontSize: "2rem", fontWeight: "bold" }}>
              {parseFloat(equtiyRS).toFixed(1)}
            </td>
            <td style={{ fontSize: "2rem", fontWeight: "bold" }}>
              {parseFloat(datRS).toFixed(1)}
            </td>
            <td style={{ fontSize: "2rem", fontWeight: "bold" }}>
              {parseFloat(naRS).toFixed(1)}
            </td>
          </tr>
        </table>
      </Card>
    </>
  );
};
export default Sector;
